<template>
<el-card class="card_back">
     <template #header>
        <el-button-group>
        <el-button type="primary" size="mini" icon="el-icon-circle-plus-outline" @click="addNewFun('appForm')">添加</el-button>
        <el-button type="primary" size="mini" icon="el-icon-edit" @click="updateFun" :disabled="editDisabled">编辑</el-button>
        <el-button type="primary" size="mini" icon="el-icon-delete" @click="deleteFun" :disabled="editDisabled">删除</el-button>
        <el-button type="primary" size="mini" icon="el-icon-circle-close-outline" :disabled="editDisabled" @click='cancelFun'>取消</el-button>
    </el-button-group>
     </template>
    <el-row>
        <el-col :span="10">
            <el-card>
                <el-form ref="appForm" :model="addInfor" :rules="rules">
                    <el-form-item label="应用系统" prop="application">
                        <el-select v-model="addInfor.application" @change="TreeNodeDataReq">
                            <el-option :value="item.application_system_id" v-for="(item,index) of applications" :key="index" :label="item.system_name"></el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
                <el-tree :data="data" :props="defaultProps" node-key="funTree" :default-expand-all='true' @node-click="nodeClick" :highlight-current="true" @node-collapse="nodeCollapse" @node-expand="nodeExpand"></el-tree>
            </el-card>
        </el-col>
        <el-col :span="11" :push="1">
            <el-card>
                <el-form ref="funForm" label-position="right" label-width="100px" :model="addInfor" :rules="rules" :disabled="formDisabled">
                    <el-form-item label="类型" prop="type">
                        <el-select v-model="addInfor.type" :disabled="!editDisabled">
                            <el-option label="目录" value="0"></el-option>
                            <el-option label="菜单" value="1"></el-option>
                            <el-option label="功能" value="2"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="父级节点id">
                        <el-input disabled v-model="addInfor.parentId" placeholder="空"></el-input>
                    </el-form-item>
                    <el-form-item label="父级功能名称">
                        <el-input disabled v-model="addInfor.parentName" placeholder="空"></el-input>
                    </el-form-item>
                    <el-form-item label="功能名称" prop="name">
                        <el-input v-model="addInfor.name" placeholder="请输入功能名称"></el-input>
                    </el-form-item>
                    <el-form-item label="路径" prop="url" v-if="addInfor.type==1">
                        <el-input v-model="addInfor.url" placeholder="请输入路径path"> </el-input>
                    </el-form-item>
                    <el-form-item label="图标" prop="icon">
                        <el-input v-model="addInfor.icon" placeholder="请选择图标"></el-input>
                    </el-form-item>
                    <el-form-item label="排序号" prop="order_num">
                        <el-input v-model="addInfor.order_num" placeholder="请输入排序号"></el-input>
                    </el-form-item>
                    <el-form-item label="授权标识" prop="perms" v-if="addInfor.type!=0">
                        <el-input v-model="addInfor.perms" placeholder="请输入授权标识"></el-input>
                    </el-form-item>
                    <el-form-item label="详情描述" prop="detail">
                        <el-input v-model="addInfor.detail" placeholder="请输入详情描述"></el-input>
                    </el-form-item>
                    <el-form-item v-show="!formDisabled">
                        <el-button type="primary" @click="dataSubmit('funForm')">提交</el-button>
                        <el-button type="warning" @click="utils.formreset($refs['funForm'])">重置</el-button>
                    </el-form-item>
                </el-form>
            </el-card>
        </el-col>
    </el-row>
</el-card>
</template>

<script>
export default {
    data() {
        let vm = this;
        return {
            formDisabled: true, //表单编辑状态
            editDisabled: true, //修改删除等功能禁止使用状态
            // newFunDisabled: true, //添加状态
            addorup: 1, //1表示新增，0表示修改 必需属性
            data: [],
            defaultProps: {
                children: 'children',
                label: 'name',
            },
            applications: [],
            addInfor: {
                id: "",
                type: "0",
                parentId: "",
                parentName: "",
                name: "",
                url: "",
                icon: "",
                order_num: "",
                application: "",
                perms: "",
                detail: ""
            },
            rules: {
                type: [{
                    trigger: 'blur',
                    required: true,
                    message: "请选择类型"
                }],
                application: [{
                    trigger: 'blur',
                    required: true,
                    message: "请选择应用系统"
                }],
                name: [{
                    trigger: 'blur',
                    required: true,
                    message: '功能名称不能为空，请输入功能名称'
                }, {
                    trigger: 'blur',
                    len: 20,
                    title: '功能名称',
                    validator: vm.$validateApi.cnLength,
                }],
                url: [{
                    trigger: 'blur',
                    required: true,
                    message: '路径不能为空，请输入路径'
                }, {
                    trigger: 'blur',
                    pattern: /^[a-zA-Z0-9]{1,20}$/,
                    message: "路径最长为20位,且只能包含为字母或数字"
                }],
                icon: [{
                    trigger: 'blur',
                    required: true,
                    message: '图标不能为空，请选择图标'
                }],
                order_num: [{
                    trigger: 'blur',
                    required: true,
                    message: '排序号不能为空，请输入排序号'
                }],
                perms: [{
                    trigger: 'blur',
                    required: true,
                    message: '授权标识不能为空，请输入授权标识'
                }],
                detail: [{
                    trigger: 'blur',
                    required: true,
                    message: '功能描述不能为空，请输入功能描述'
                }]
            }
        }
    },
    mounted() {
        this.applicationReq();
    },
    watch: {
        'addInfor.type'(news) {
            let application = this.addInfor.application;
            this.utils.formreset(this.$refs['funForm']);
            this.addInfor.type = news;
            this.addInfor.application = application;
        }
    },
    methods: {
        //data注入
        dataInjection(data) {
            this.addInfor = this.utils.object.copy(data);
            this.$refs['funForm'].model = this.addInfor;
        },
        //formDisabled状态改变
        formDisableToggle(bool = true) {
            this.formDisabled = bool;
        },
        //editDisabled状态改变
        editDisableToggle(bool = true) {
            this.editDisabled = bool;
        },
        //添加按钮绑定函数
        addNewFun(ref) {
            let vm = this;
            console.log(vm.$refs[ref].validateField(['application'], (res, object) => {
                if (res == "") {
                    let application = this.addInfor.application;
                    vm.dataInjection({
                        type: '0',
                        application: application
                    });
                    vm.editDisableToggle(true);
                    vm.formDisableToggle(false);
                    vm.addorup = 1;
                }
            }))
        },
        //修改按钮绑定函数
        updateFun() {
            this.formDisableToggle(false);
            vm.addorup = 0;
        },
        //取消按钮绑定函数
        cancelFun() {
            let vm = this;
            vm.editDisableToggle(true);
            vm.formDisableToggle(true);
        },
        //删除按钮绑定函数
        deleteFun() {
            let vm = this;
            let datas = {
                id: vm.addInfor.id,
            };
            vm.$api.deleteRequset('/deleteFunctionData.action', vm.utils.data.formatter(datas));
        },
        //应用系统查询
        applicationReq() {
            let vm = this;
            vm.$api.getData('/selectApplicationId.action').then(success => {
                if (success.bool) {
                    vm.applications = success.data;
                }
            })
        },
        //树形控件数据获取
        TreeNodeDataReq() {
            let vm = this;
            vm.$api.getData('/selectFunctionPaging.action').then(success => {
                if (success.bool) {
                    vm.data = success.data;
                }
            })
        },
        //树型控件节点点击调用函数
        nodeClick(data, node, tree) {
            let vm = this;
            if (node.childNodes.length > 0) {
                return;
            } else {
                vm.dataInjection(data);
                vm.editDisableToggle(false);
                vm.formDisableToggle(true);
            }
        },
        // 树形控件节点展开函数
        nodeExpand(data, node, tree) {
            let vm = this;
            vm.dataInjection(data);
            vm.editDisableToggle(false);
        },
        //树形控件节点合并函数
        nodeCollapse(data, node, tree) {},
        //数据提交
        dataSubmit() {
            let vm = this;
            let addorup = vm.addorup;
            vm.$refs[ref].validate((valreq) => {
                if (valreq) {
                    vm.$api.opreateRequset(addorup ? '/addFunction.action' : '/updatePageInf.action', vm.utils.data.formatter(vm.addInfor), addorup ? '添加' : '修改').then(success => {
                        if (success) {
                            vm.utils.formreset(vm.$refs[ref]);
                            vm.editDisableToggle(false);
                            vm.formDisableToggle(true);
                        }
                    });
                }
            })
        },
    }
}
</script>

<style>
</style>
